.App {
  background-image: url("./Back.jpg");
  background-repeat: no-repeat;
  background-size: cover;
}

.navbar {
  float: right;
  height: 60px;
  width: 100%;
  background-color: transparent;
  padding-top: 30px;
  position: fixed;
}

.navbar>span {
  padding: 0 12px 10px 16px;
  float: right;
  color: white;
}

.navbar>span>a {
  text-decoration: none;
  color: white;
}

.cover {
  height: 90vh;
  width: 100%;
}

/**
.info {}
**/
.details,
.news {
  margin: 60px 80px 60px 80px;
  padding: 20px 15px 20px 15px;
}

.info>.details>p,
.info>.news>p {
  padding: 20px 15px 20px 15px;
  color: white;
  font-family: cursive;
  font-size: 20px;
}

.org {
  margin: 60px 80px 60px 80px;
  padding: 20px 15px 20px 15px;
}

.org>p {
  padding: 5px 7px 5px 7px;
  color: white;
  font-family: cursive;
  font-size: 20px;
}

.overview,
.guideline {
  margin: 0 80px 60px 80px;
  padding: 20px 15px 20px 15px;
}

.overview>p,
.guideline>p {
  margin-top: 20px;
  padding: 10px 12px 10px 12px;
  color: white;
  font-family: cursive;
  font-size: 20px;
}

.footer {
  height: 150px;
  width: 100%;
  background-image: url("./footer.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  color: white;
  text-align: center;
  font-size: 18px;
}

.reg {
  margin: auto;
  width: 30%;
  padding: 25px;
  border: 3px solid white;
  border-radius: 20px;
}

.input {
  margin: 28px;
  width: 80%;
  height: 40px;
  border-radius: 10px;
  padding: 5px;
  font-size: 20px;
  font-family: cursive;
  background-color: transparent;
  color: white;
}

.submit {
  margin: 28px 30px 28px 30px;
  width: 40%;
  height: 40px;
  border-radius: 20px;
  font-size: 20px;
  font-family: cursive;
  color: green;
}

.submit:hover {
  cursor: pointer;
  background-color: transparent;
  color: white;
}

.reg-header > h1 {
  margin: auto;
  width: 20%;
  padding: 10px;
  color: white;
  font-size: 30px;
  text-align: center;
}

.reg-footer > h3 {
  margin: auto;
  width: 20%;
  padding: 10px;
  color: white;
  text-align: center;
}

.title > b{
  background-color: white;
  color: black;
  width: 30%;
  border-radius: 10px;
  padding: 7px;
}